import React from "react";
import { Button, Form, FormGroup, Label, Input, FormText } from "reactstrap";
import MailchimpSubscribe from "react-mailchimp-subscribe";
import { Formik } from "formik";

const CustomForm = ({ status, message, onSubmit, ...props }) => {
  return (
    <>
      <p>
          {status}
      </p>

      <p>
          {message}
      </p>

      <Formik
        initialValues={{ email: '' }}
        onSubmit={async (values, actions) => {
          try {
              await onSubmit(values);
          } catch(e) {
              console.error(e);
          }
        }}
        render={props => (
          <form onSubmit={props.handleSubmit}>
            <input
              type="text"
              onChange={props.handleChange}
              onBlur={props.handleBlur}
              value={props.values.name}
              name="name"
            />
            {props.errors.name && <div id="feedback">{props.errors.name}</div>}
            <button type="submit">Submit</button>
          </form>
        )}
      />
    </>
  );
};

export const MailChimpForm = props => {
  const url =
    "//twitter.us14.list-manage.com/subscribe/post?u=c995eaf6129ecf4b13d8ae469&id=dd2d98db0f";
  return (
    <div>
      <MailchimpSubscribe
        url={url}
        render={({ subscribe, status, message }) => (
          <CustomForm
            status={status}
            message={message}
            onSubmit={async formData => await subscribe(formData)}
          />
        )}
      />
    </div>
  );
};
